.is-overflow-hidden {
	overflow: scroll; }

body {
	min-width: 320px;
	width: 100%;
	font-family: 'Roboto', sans-serif;
	@include f(15px,500,1.73);
	color: #5B5E77;
	@include font-smoothing;
	background-color: #F7F7F9;
	@include fadeout(.3s);
	@include sm {
		font-size: 14px; }
	&.is-loaded {
		@include fadein; }
	.is-open-menu & {
		width: 100%; } }


.title-main {
	@include fny;
	@include css-lock(60,100,1023,1440);
	letter-spacing: -0.01em;
	line-height: 0.83;
	@include sm {
		font-size: 29.3px;
		text-align: center;
		line-height: 1.1; } }


.title-main-md {
	@include fny;
	color: #FFFFFF;
	letter-spacing: -0.36px;
	line-height: 1.4;
	@include css-lock(23,40,1023,1440); }

.title {
	font-family: 'Roboto', sans-serif;
	@include f(30px,700,1.17);
	letter-spacing: 0;
	text-align: center;
	@include sm {
		font-size: 26px; } }

.text-uppercase {
	text-transform: uppercase; }

.text-center {
	text-align: center; }

.text-left {
	text-align: left; }

.flex-end {
	align-self: flex-end; }

.loader {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 99999;
	width: 100%;
	height: 100vh; }
