.btn {
	padding: 17px 30px 15px;
	font-family: 'Roboto', sans-serif;
	@include f(15px,700,1.2);
	min-width: 243px;
	border-radius: 28px;
	text-align: center;
	color: #fff;
	background-color: #6E7FFC;
	transition: background-color .3s, color .3s;
	display: inline-block;
	@include sm {
		min-width: 100%; }
	@include hover {
		background-color: darken(#6E7FFC, 10%); }
	&_white {
		color: #2C253B;
		background-color: #fff;
		@include hover {
			color: #fff;
			background-color: #6E7FFC; } }
	&_full {
		width: 100%; }
	&_bdsm {
		border-radius: 5px; } }
