.header {
	padding: 22px 0;
	@include f(16px, 500, 1.1);
	@include fnr;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	@include md {
		padding: 6px 0; }
	&:not(.header_main) {
		color: #1B273D;
		background-color: #F7F7F9; }
	&_main {
		color: #fff;
		@include md {
			background: #2A324C; } }
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__logo {
		@include md {
			margin: 0 auto; } }
	&__nav {
		@include md {
			position: absolute;
			top: 0;
			left: 0;
			width: 100vw;
			height: 100vh;
			overflow: auto;
			margin-left: -100vw;
			transition: transform .4s ease-in-out;
			background-color: #fff; }
		.header_main & {
			@include md {
				background-color: #2A324C; } }
		.is-open-menu & {
			@include md {
				transform: translateX(100vw); } } }
	&__phone {
		color: currentColor;
		transition: opacity .3s;
		@include hover {
			opacity: .7; } }
	&__title {
		margin: 0 auto 50px;
		max-width: 270px;
		@include fny;
		font-size: 30px;
		line-height: 1;
		letter-spacing: -0.002em;
		text-align: center;
		display: none;
		@include md {
			display: block; }
		// .header_main &
 } }		// 	background-color: #fff
.logo {
	position: relative;
	z-index: 1;
	svg {
		@include md {
			width: 32px;
			height: 39px; }
		path {
			.header_main & {
				fill: #fff !important; } } } }

.nav {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	@include md {
		padding: 70px 0 120px;
		text-align: center;
		display: block; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		@include md {
			flex-direction: column;
			width: 100%;
			text-align: center; }
		&:not(:last-child) {
			@include css-lock(30,70,1024,1440,margin-right);
			@include md {
				margin: 0 0 50px; } } }
	&__item {
		position: relative;
		display: block;
		&:not(:last-child) {
			@include css-lock(30,70,1024,1440,margin-right);
			@include md {
				margin: 0 0 50px; } }
		@include hover {
			@include rmin(1024) {
				.nav__drop {
					@include fadein; } } } }
	&__link {
		color: currentColor;
		opacity: .5;
		transition: opacity .3s;
		@include hover {
			opacity: .7; }
		&.is-active {
			opacity: 1; }
		&.is-lg-disabled {
			@include rmin(1024) {
				pointer-events: none; } } }
	&__drop {
		position: absolute;
		top: 100%;
		left: 50%;
		@include fnr;
		font-size: 16px;
		width: 327px;
		color: #1B273D;
		padding: 29px 34px 37px;
		transform: translateX(-50%);
		margin-top: 23px;
		background-color: #F7F7F9;
		box-shadow: 20px 22px 44px -20px rgba(0,0,0,0.31);
		border-radius: 20px;
		@include fadeout(.3s);
		&:before {
			position: absolute;
			content: '';
			top: -23px;
			left: 0;
			right: 0;
			bottom: 100%; }
		&_md {
			width: 596px; }
		li {
			display: block;
			&:not(:last-child) {
				margin-bottom: 8px; } }
		a {
			color: currentColor;
			transition: opacity .3s;
			@include hover {
				opacity: .7; } } }
	&__title {
		font-size: 24px;
		margin-bottom: 30px; }
	&__row {
		margin: 0 -15px;
		display: flex; }
	&__col {
		padding: 0 15px;
		@include fs(50%);
		&_full {
			@include fs(100%); } } }
.btn-menu {
	position: relative;
	z-index: 1;
	width: 13px;
	height: 11px;
	background-color: transparent;
	display: none;
	@include md {
		display: inline-block; }
	&:before {
		position: absolute;
		content: '';
		top: -10px;
		left: -10px;
		right: -10px;
		bottom: -10px; }
	&__line {
		position: absolute;
		left: 0;
		width: 13px;
		height: 1px;
		background-color: #1B273D;
		transform-origin: left;
		transition: transform .4s, width .4s, opacity .4s;
		@include gpu;
		.header_main & {
			background-color: #fff; }
		&:first-child {
			top: 0;
			.is-open-menu & {
				transform: rotate(45deg);
				width: 14px; } }
		&:nth-child(2) {
			top: 5px;
			.is-open-menu & {
				transform: scale(0);
				opacity: 0; } }
		&:last-child {
			bottom: 0;
			.is-open-menu & {
				transform: rotate(-45deg);
				width: 14px; } } } }
