.pagination__prev .icon, .pagination__next .icon { vertical-align: middle; font-size: 13px; display: inline-block; }

.pagination__prev { margin-right: 10px; }

.pagination__prev-text { margin-left: 15px; }

.pagination__prev .icon { transform: rotate(180deg); }

.pagination__next { margin-left: 10px; }

.pagination__next-text { margin-right: 15px; }

.pagination__list { flex-wrap: wrap; justify-content: center; display: flex; }

.pagination__list li.is-active a, .pagination__list li.active a { opacity: 1; }

.pagination__list li:not(:last-child) { margin-right: 10px; }

.pagination__list a { opacity: .4; transition: opacity .3s; }

.no-touch .pagination__list a:hover { opacity: 1; }

.pagination__list span { opacity: .4; }

.subscribe { padding-top: 45px; border-top: 1px solid #D6D6D6; align-items: center; display: inline-flex; }

@media only screen and (max-width: 767px) { .subscribe { padding-top: 20px; flex-direction: column; display: flex; } }

.subscribe__text { margin-right: 10px; }

@media only screen and (max-width: 767px) { .subscribe__text { margin: 0 0 20px; } }

.subscribe__control { margin-right: 10px; padding: 15px; width: 260px; height: 50px; border-radius: 27px; letter-spacing: 0; align-items: center; border: 1px solid #DADADA; display: inline-flex; }

@media only screen and (max-width: 767px) { .subscribe__control { margin: 0 0 20px; width: 100%; } }

.subscribe__label { flex-shrink: 0; margin-right: 10px; user-select: none; cursor: pointer; }

.subscribe__input { flex-grow: 0; width: 100%; }

.subscribe__input input { height: 26px; width: 100%; font-weight: 500; line-height: 1.73; font-size: 15px; color: #6E7FFC; letter-spacing: 0; font-family: 'Roboto', sans-serif; border-color: transparent; display: inline-block; }

.subscribe__input input::placeholder { color: rgba(110, 127, 252, 0.5); }

.subscribe__input input:focus::placeholder { font-size: 0; }

@media only screen and (max-width: 767px) { .subscribe__button { width: 100%; } }

.addresses { margin: 0 -30px; flex-wrap: wrap; display: flex; }

.addresses__col { margin-top: 40px; padding: 0 30px; flex: 0 0 33.3%; max-width: 33.3%; }

@media only screen and (max-width: 1023px) { .addresses__col { flex: 0 0 50%; max-width: 50%; } }

@media only screen and (max-width: 767px) { .addresses__col { flex: 0 0 100%; max-width: 100%; } }

.address { padding: 30px 30px 45px; height: 100%; background-color: #F7F7F9; }

.address__title { margin-bottom: 20px; }

.feetback { display: flex; }

@media only screen and (max-width: 1023px) { .feetback { flex-direction: column; } }

.feetback__left { padding: 103px 90px 90px; flex-shrink: 0; width: 410px; color: #fff; background-image: linear-gradient(134deg, #4D4063 0%, #282136 100%); }

@media only screen and (max-width: 1023px) { .feetback__left { width: 100%; } }

@media only screen and (max-width: 767px) { .feetback__left { padding: 30px 15px; } }

.feetback__title-bord { margin-bottom: 25px; padding-bottom: 20px; border-bottom: 1px solid rgba(255, 255, 255, 0.1); }

.feetback__form { padding: 48px 109px 39px; flex-grow: 1; }

@media only screen and (max-width: 767px) { .feetback__form { padding: 30px 15px; } }

.form__title-sm { margin-bottom: 28px; font-family: 'Roboto', sans-serif; font-size: 20px; font-weight: 700; line-height: 1.2; letter-spacing: 0; text-align: center; }

.form__title { margin-bottom: 37px; }

.form-field:not(:last-child) { margin-bottom: 35px; }

.form-control input, .form-control textarea { height: 36px; width: 100%; color: #5B5E77; transition: border-color .3s; font-family: 'Roboto', sans-serif; font-size: 15px; font-weight: 700; line-height: 1.2; border-bottom: 1px solid #9393A5; display: block; }

.no-touch .form-control input:hover, .no-touch .form-control textarea:hover { border-color: #6AA2FF; }

.form-control input::placeholder, .form-control textarea::placeholder { color: #5B5E77; }

.form-control input:focus, .form-control textarea:focus { border-color: #6AA2FF; }

.form-control input:focus::placeholder, .form-control textarea:focus::placeholder { font-size: 0; }

.form-control input { padding-bottom: 17px; }

.form-control textarea { padding: 20px; height: 82px; resize: none; border-radius: 8px; border-bottom: 1px solid #9393A5; }

.label { margin-bottom: 17px; color: #5B5E77; cursor: pointer; user-select: none; font-family: 'Roboto', sans-serif; font-size: 15px; font-weight: 700; line-height: 1.13; display: block; }

.form-check:not(:first-child) { margin-top: 15px; }

.form-footer:not(:first-child) { margin-top: 28px; }

.checkbox { position: relative; vertical-align: top; cursor: pointer; user-select: none; display: inline-block; }

.checkbox input { position: absolute; opacity: 0; visibility: hidden; }

.checkbox input:checked ~ .checkbox__check { opacity: 1; visibility: visible; }

.checkbox__title { padding-left: 25px; font-size: 10px; font-family: 'Roboto', sans-serif; font-weight: 500; letter-spacing: 0; }

.checkbox__title:before { position: absolute; content: ''; top: 4px; left: 0; height: 20px; width: 20px; border-radius: 2px; border: 1px solid #9393A5; }

.checkbox__check { position: absolute; top: 0; left: 3px; opacity: 0; visibility: hidden; transition: opacity 0.3s, visibility 0.3s; }

[data-modal] { position: fixed; z-index: -1; top: 0; left: 0; right: 0; bottom: 0; opacity: 0; visibility: hidden; background-color: rgba(0, 0, 0, 0.6); user-select: none; padding: 15px; text-align: center; font-size: 0; transform: translate3d(0, 0, 0); overflow: auto; transition: opacity 0.3s, visibility 0.3s, z-index 0.3s; }

[data-modal]:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

[data-modal].is-open { opacity: 1; visibility: visible; z-index: 100; }

[data-modal-container] { position: relative; padding: 70px 100px; z-index: 1; font-size: 15px; text-align: left; vertical-align: middle; display: inline-block; max-width: 572px; width: 99.9%; background-color: #fff; border: 2px solid #E8E8E8; }

@media only screen and (max-width: 767px) { [data-modal-container] { padding: 30px 15px; } }

[data-modal-close] { position: absolute; top: -13px; right: -13px; background-color: #B6B8CB; width: 43px; height: 43px; border-radius: 50%; transition: background-color .3s; text-align: center; font-size: 0; }

.no-touch [data-modal-close]:hover { background-color: #7a7ea1; }

[data-modal-close] .icon { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: 20px; color: #fff; }

.footer { padding: 40px 0; text-align: center; color: #fff; font-family: 'PT Sans', sans-serif; font-size: 14px; line-height: 1.2; background-image: linear-gradient(134deg, #4D4063 0%, #282136 100%); }

@media only screen and (max-width: 767px) { .footer { padding: 30px 0; } }

.footer__inner { margin: 0 auto; padding: 0 15px; max-width: 748px; }

.footer__logo { margin-bottom: 43px; }

@media only screen and (max-width: 767px) { .footer__logo { margin-bottom: 30px; } }

.footer__row { padding: 13px 0; align-items: center; display: flex; border-top: 1px solid rgba(255, 255, 255, 0.1); border-bottom: 1px solid rgba(255, 255, 255, 0.1); }

.footer__row:not(:last-child) { margin-bottom: 23px; }

.footer__col { position: relative; flex: 0 0 50%; max-width: 50%; text-align: left; }

.footer__col:first-child { padding-right: 30px; text-align: right; }

@media only screen and (max-width: 767px) { .footer__col:first-child { padding-right: 15px; } }

.footer__col:last-child { padding-left: 30px; border-left: 1px solid #fff; }

@media only screen and (max-width: 767px) { .footer__col:last-child { padding-left: 15px; } }

.footer__col a { color: currentColor; transition: opacity .3s; }

.no-touch .footer__col a:hover { opacity: .7; }

.footer__soc:not(:last-child) { margin-bottom: 23px; }

.footer__copy { font-size: 14px; font-size: 14px; color: #FFFFFF; text-transform: lowercase; letter-spacing: 0; text-align: center; }

.logo-text { display: inline-flex; font-family: 'New York', sans-serif; font-weight: 500; align-items: center; font-size: 29px; color: #FFFFFF; letter-spacing: -0.002em; text-align: left; color: #fff; line-height: 1; }

.logo-text__img { width: 53px; height: 65px; flex-shrink: 0; }

.logo-text__img img { max-width: 100%; display: block; }

.logo-text__text { padding-left: 20px; flex-grow: 1; max-width: 353px; letter-spacing: -0.03em; }

@media only screen and (max-width: 767px) { .logo-text__text { display: none; } }

.soc { display: flex; justify-content: center; flex-wrap: wrap; }

.soc li { position: relative; width: 30px; height: 30px; overflow: hidden; border-radius: 30px; }

.soc li:not(:last-child) { margin-right: 40px; }

@media only screen and (max-width: 767px) { .soc li:not(:last-child) { margin-right: 15px; } }

.soc_sm li:not(:last-child) { margin-right: 20px; }

.soc a { color: currentColor; transition: opacity .3s; display: block; }

.no-touch .soc a:hover { opacity: .7; }

.soc img { max-width: 100%; display: block; }
